/* eslint-disable */
import { ISSUE_ADD_API,ISSUE_INFO_API} from './config'
import { SERVER_HOST } from '@/config/host'
import http from '@/utils/request'

export function addIssue (data) {
  return new Promise((resolve, reject) => {
    // console.log(userInfo)
    http.post(SERVER_HOST + ISSUE_ADD_API, data).then(res => {
      console.log(res)
      resolve(res)
    }).catch(err => {
      // for Debug
      reject(err)
      console.log(err)
    })
  })
}

// 查詢issue數據
export function findIssueInfo (pageSize , currentPage) {
  return new Promise((resolve, reject) => {
    // console.log(userInfo)
    http.get(SERVER_HOST + ISSUE_INFO_API + '?limit=' + pageSize+'&skip=' + (currentPage -1) * pageSize).then(res => {
      // console.log(res)
      resolve(res)
    }).catch(err => {
      // for Debug
      reject(err)
      console.log(err)
    })
  })
}


