<template>
  <div>
  <div style="display:flex;justify-content: space-between;margin-bottom:10px">
  <div style="display:flex">
    <span class="title" style="margin-left: 20px;">日期 :</span>
    <el-date-picker
     style="width: 160px"
      v-model="valueDate"
      type="date"
      placeholder="选择日期">
    </el-date-picker>
  </div>
   <div>

  <el-button class="exportExcel"  @click="_exportExcel" style="margin-right: 20px;text-align:right;background:#77A1D3"  type="primary" icon="el-icon-download">導出Excel</el-button>
  <el-button class="exportExcel" @click="_addItem" style="margin-right: 20px;text-align:right;background:#20BDFF"  type="primary" icon="el-icon-plus">新增issue</el-button>
  <el-button type="primary" style="background:#3CA55C" icon="el-icon-search" @click="_search">搜索</el-button>

   </div>
</div>
    <el-table
     v-if ="tableData.length"
     v-loading = "pictLoading"
      element-loading-background = "rgba(0, 0, 0, 0.5)"
      element-loading-text = "数据正在加载中"
      element-loading-spinner = "el-icon-loading"
     :stripe= "true"
     :border="true"
    :data="tableData"
    highlight-current-row
    style="width: 100%">
    <el-table-column
      type="index"
      label="序號"
      width="55">
    </el-table-column>
    <el-table-column
      property="line"
      label="線別"
     >
    </el-table-column>
    <el-table-column
      property="stack"
      label="站別"
   >
   </el-table-column>
    <el-table-column
      property="machineNum"
      label="機種"
   ></el-table-column>
    <el-table-column
      property="barcode"
      label="條碼"
   ></el-table-column>
    <el-table-column
      property="imgUrl"
      label="模擬圖"
   >
    <template   slot-scope="scope">
          <img v-if="scope.row.imgUrl" :src="scope.row.imgUrl"  min-width="70" height="70" />
        </template>
   </el-table-column>
    <el-table-column
      property="imgUrlRes"
      label="結果圖"
   >
    <template   slot-scope="scope">
          <img v-if="scope.row.imgUrlRes" :src="scope.row.imgUrlRes"  min-width="70" height="70" />
        </template>  </el-table-column>
    <el-table-column
      property="description"
      label="description"
   ></el-table-column>
    <el-table-column
      property="user"
      label="人員"
   ></el-table-column>
    <el-table-column
      property="happenDate"
      label="異常發生時間"
   >
    </el-table-column>
    <el-table-column
      property="username"
      label="上傳人員"
   >
    </el-table-column>
    <el-table-column
      property="createDate"
      label="創建時間"
   >
    </el-table-column>
  </el-table>
  <BasePagination v-if="tableData.length" @handleCurrentChange="handleCurrentChange" @handleSizeChange="handleSizeChange" :total="total"/>
  <el-dialog
  title="新增異常數據"
  :close-on-click-modal="false"
  :visible.sync="dialogVisible"
  width="1200px"
  >
  <div style="display:flex;justify-content:space-around">
    <div>
  <div style="margin-top: 10px">
    <span class="title">線別 :</span><el-select
        v-model="issue.line"  :clearable="true">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
  </div>
  <div style="margin-top: 10px">
    <span class="title">機種:</span><el-input v-model="issue.machineNum" style="width: 220px"></el-input>
  </div>
  <div style="margin-top: 10px">
    <span class="title">條碼:</span><el-input v-model="issue.barcode" style="width: 220px"></el-input>
  </div>
  <div style="margin-top: 10px">
    <span class="title">描述:</span><el-input v-model="issue.description" style="width: 220px"></el-input>
  </div>
  <div style="margin-top: 10px">
    <span class="title">人員:</span><el-input v-model="issue.user" style="width: 220px"></el-input>
  </div>
    <div style="display:flex;margin-top:10px">
    <span class="title">站別:</span><el-input style="width: 220px" v-model="issue.stack"></el-input>
  </div>
  <div style="display:flex;margin-top:10px">
  <span class="title" >日期 :</span><el-date-picker
      v-model="issue.happenDate"
      type="date"
      placeholder="选择日期">
  </el-date-picker>
  </div>
    </div>
    <div>
      <div style="margin-top: 10px;text-align:center">
      <ImgCutter rate="1:1" v-on:cutDown="cutDown" label="點擊模擬圖片"></ImgCutter>
      <img style="margin-top: 20px" :src="imgUrl1" width="200px" height="200px" alt="模擬圖片">
      </div>
    </div>
    <div>
      <div style="margin-top: 10px;text-align:center" >
      <ImgCutter  rate="1:1"  v-on:cutDown="resultImage" label="點擊測試結果圖片"></ImgCutter>
      <img style="margin-top: 20px" :src="imgUrl2" width="200px" height="200px" alt="測試結果圖片">
      </div>
    </div>
  </div>
  <div>
  </div>
  <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="uploadIssueInfo">确 定</el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>
/* eslint-disable */
import BasePagination from '@/components/base/basePagination'
import DataTable from '@/components/datatable'
import ImgCutter from 'vue-img-cutter'
import {uploadImage} from '../../api/upload'
import{ addIssue, findIssueInfo} from '../../api/issue'
import {utc2beijing} from '../../utils/date'
import table2excel from 'js-table2excel'
import {mapState, mapGetters} from 'vuex'
export default {
  data () {
    return {
      imgUrl1: '',
      imgUrl2: '',
      dialogVisible: false,
      issue: {
        stack: '',
        happenDate: new Date(),
        line: '',
        machineNum: '',
        barcode: '',
        description: '',
        user: '',
        imgUrl: '',
        imgUrlRes: ''
      },
      valueDate: '',
      value: '',
      dialogVisible: false,
      coverVal: '',
      options: [
        {
          label: 'A2A',
          value: 'A2A'
        },
        {
          label: 'A2B',
          value: 'A2B'
        }
      ],
      coverOptions: [
        {
          label: '可Cover',
          value: 'normalleave'
        },
        {
          label: '不可Cover',
          value: 'errorleave'
        }
      ],
      // 员工工号
      pictLoading: false,
      tableData: [],
      currentPage: 1,
      pageSize: 10,
      userNum: '',
      startDate: '',
      total: 0,
      endDate: '',
      tableBarList: [
        {label: '序號', width: '50px', type: 'index'},
        {property: 'line', label: '線別'},
        {property: 'stack', label: '站別'},
        {property: 'machineNum', label: '機種'},
        {property: 'barcode', label: '條碼'},
        {property: 'imgUrl', label: '模擬圖'},
        {property: 'imgUrlRes', label: '結果圖'}, 
        {property: 'description', label: '描述'},
        {property: 'user', label: '人員'},
        {property: 'happenDate', label: '異常發生時間'},
      ],
      pickerOptionsStart: {
        disabledDate (time) {
          if (!time) {
            return ''
          }
          return time.getTime() > Date.now()
        }
      },
      pickerOptionsEnd: {
        disabledDate (time) {
          if (!time) {
            return ''
          }
          return time.getTime() > Date.now()
        }
      }
    }
  },
  methods: {
    uploadIssueInfo () {
       if (this.isLogin) {
          addIssue(Object.assign({},this.issue, {username: this.userInfo.username})).then(res => {
            if (!res.data) {
              this.$message({
                message: 'token过期，请重新登录',
                type: 'warning'
              })
            }else{
              if (res.data === 'no_exist') {
                this.$message({
                  message: '用户名不存在',
                  type: 'warning'
                })       
              }else {
                this.$message({
                  message: '上傳成功!',
                  type: 'success'
                })    
              }

            }
          }).catch(err=>{
            console.log(err)
          })
       } else {
        this.$message({
          message: '请登录',
          type: 'warning'
        })
       }

    },
    resultImage (data) {
      // console.log(data)
      let fd = new FormData()
      fd.append("image", data.blob);
      // console.log(fd)
      // fd.append("originalname", data.fileName)
      let that = this
      uploadImage(fd).then(res=> {
         that.issue.imgUrlRes =res.data.url
      })
      this.imgUrl2 =  data.dataURL
    },
    cutDown (data) {
      console.log(data)
      let fd = new FormData()
      fd.append("image", data.blob);
      // console.log(fd)
      // fd.append("originalname", data.fileName)
       let that = this
      uploadImage(fd).then(res=> {
        that.issue.imgUrl =res.data.url
      })
      this.imgUrl1 =  data.dataURL
    },
    _addItem () {
      this.dialogVisible = true
    },
    changeStart () {
      if (this.startDate === '' || this.startDate === null) {
        this.endDate = ''
        return
      }
      const that = this
      this.pickerOptionsEnd = {
        disabledDate (time) {
          if (that.startDate === '' || that.startDate === null) {
            return
          }
          return time.getTime() > that.startDate.getTime() + 3600 * 1000 * 24 * 150 || time.getTime() < that.startDate.getTime()
        }
      }
    },
    _search () {
      this.pictLoading = true
      findIssueInfo(this.pageSize,this.currentPage).then(res =>{
        res.data.data.forEach(item => {
          item.happenDate = utc2beijing(item.happenDate)
          item.createDate = utc2beijing(item.createDate)
        });
        this.tableData = res.data.data
        this.total = res.data.total
        this.pictLoading = false
      })
    },
    _exportExcel () {
      const column = [
        {title: '線別', key: 'line', type: 'text'},
        {title: '站別', key: 'stack', type: 'text'},
        {title: '機種', key: 'machineNum', type: 'text'},
        {title: '條碼', key: 'barcode', type: 'text'},
        {title: '模擬圖', key: 'imgUrl', type: 'image',width: 200,height: 200},
        {title: '結果圖', key: 'imgUrlRes', type: 'image',width: 200,height: 200},
        {title: '描述', key: 'description', type: 'text'},
        {title: '人員', key: 'user', type: 'text'},
        {title: '異常發生時間', key: 'happenDate', type: 'text'},
        {title: '創建時間', key: 'createDate', type: 'text'},
      ]
      // const tHeader = ['線別', '站別', '機種', '條碼', '模擬圖', '結果圖', '描述', '人員', '異常發生時間']
      // const filterVal = ['line', 'stack', 'machineNum', 'imgUrl', 'imgUrlRes', 'description', 'user', 'happenDate']
      const tabelName = '異常統計表'
          // 下载excel选中表单内容
      if (this.tableData.length) {
        table2excel(column, this.tableData, tabelName)
      } else {
        this.$message({
          message: 'Please select at least one item',
          type: 'warning'
        })
      }
    },
    handleSizeChange (size) {
      this.currentPage = 1
      this.pageSize = size
      this._search()
    },
    handleCurrentChange (index) {
      this.currentPage = index
      this._search()
    },
  },
  mounted() {
    this._search()
  },
  computed: {
    ...mapState({
      isLogin: state => state.isLogin
    }),
    ...mapGetters([
      'userInfo'
    ])
  },
  components: {
    // DateAndTitle,
    BasePagination,
    DataTable,
    ImgCutter
  }

}
</script>

<style lang="scss" scoped>
.title {
  display: inline-block;
  height: 40px;
  line-height: 40px;
  color: #fff;
  width: 60px;
  text-align: center;
  font-size: 14px;
  padding: 0 5px;
  background: #409eff;
  // margin-left: 10px;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
}
.idCon{
    display: inline-block;
    font-size: 0;
}
// 截图
.cropper-content {
    .cropper {
        width: auto;
        height: 300px;
    }
}
>>>.el-input__inner{
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
</style>



